import React, { useState, useEffect } from 'react';
import { BrowserRouter, Route, Link, Router, Redirect, useHistory } from 'react-router-dom';
import { api } from './urlapi';
import Swal from 'sweetalert2'
import './css/showingredient.css';
import Navbarmain from './navbarmain';
import axios from 'axios';


function Showingredient() {
    const history = useHistory();
    const [types, setTypes] = useState([]);
    var arrayypes = [];

    const [search, setSearch] = useState([]);
    const [input, setInput] = useState("");

    useEffect(() => { // Search Foods
        if (input == "") {
            setSearch(types);
            console.log("if");
        } else {
            setSearch(types.filter((res) => {
                return res.type.toLowerCase().includes(input);
            }))
            console.log("else");
        }
    }, [input]);

    useEffect(() => {
        axios.get(api + "gettype").then((res) => {
            setTypes(res.data);
        });
    },[]);
    //console.log(types);
    return (
        <div className="body-showingredie">
        <Navbarmain  />
            <div className="title-showingredie">
                <div className="title-ingredient">
                    <h1>วัตถุดิบทั้งหมด</h1>
                    <div class="field">
                        <p class="control has-icons-right sea-text">
                            <input 
                                class="input borderin"
                                type="text"
                                placeholder="Search"
                                onChange={(data) => setInput(data.target.value)}
                            />
                            <span class="icon is-small is-right">
                                <i class="fas fa-search search-i"></i>
                            </span>
                        </p>
                    </div>
                </div>
                <div className="icon-area">

                        <div  className="all-item">
                            <ul>
                                {(input == "") ? types.map((data,key)=>{
                                    return (
                                        <li onClick={() => history.push("/ingredients?id="+ data.type_id)} className="item-ing">{key+1} . {data.type} </li>

                                    )
                                }) : search.map((data,key)=>{
                                    return(
                                        <li onClick={() => history.push("/ingredients?id="+ data.type_id)} className="item-ing">{key+1} . {data.type} </li>

                                    )
                                })  }
                                
                            </ul>
                        </div>
                        
                </div>
            </div>

        </div>
    )
}

export default Showingredient;