

import { BrowserRouter, Route, Link, Router, Redirect, useHistory } from 'react-router-dom';

import StyledFirebaseAuth from 'react-firebaseui/StyledFirebaseAuth';
import firebase from 'firebase';
import React, { useState, useEffect } from 'react';
import Navbarmain from './navbarmain';
import axios from 'axios';
import { api } from './urlapi';
import coin from '../img/coin.png'
import gda from '../img/GDA.png'
import '../component/css/menu-store.css'

function MenuStore() {
    let history = useHistory();
    var str = "";
    // str = history.location.search;
    // var userID = str.substr(str.indexOf("userid") + 7, str.length);
    // console.log("userID = " + userID);
    const [statestore, setStateStore] = useState(0);
    const [foods, setFoods] = useState([]);
    const [userID, setUserid] = useState(0);

    const [isSignedIn, setIsSignedIn] = useState(false); // Local signed-in state.
    const [user, setUser] = useState([]);

    const [store, setStore] = useState([]);

    const [search, setSearch] = useState([]);
    const [input, setInput] = useState("");

    useEffect(() => { // Search Foods
        if (input == "") {
            setSearch(store);
        } else {
            setSearch(store.filter((res) => {
                return res.namestore.toLowerCase().includes(input);
            }))
        }
    }, [input]);
    
    useEffect(() => {
        axios.get(api + "getstores").then((res) => {
            setStore(res.data);
        });
    }, []);


    useEffect(() => {
        console.log(store);
    }, [store]);

    return (
        <div className="body-MenuStores">
            <Navbarmain />
            <div className="body-MenuStore">
                <div className="title-MenuStore">
                    <h1>ร้านอาหารทั้งหมด</h1>
                    <div class="field">
                        <p class="control has-icons-right sea-text">
                            <input
                                class="input borderin"
                                type="text"
                                placeholder="Search"
                                onChange={(data) => setInput(data.target.value)}
                            />
                            <span class="icon is-small is-right">
                                <i class="fas fa-search search-i"></i>
                            </span>
                        </p>
                    </div>
                </div>

                <div className="result-MenuStore">
                    <span>
                        <h2>Result</h2>
                        <h3>ทั้งหมด</h3>
                    </span>
                    {(input == "") ? store.map((data, key) => {
                        return (
                            <Link to={{
                                pathname: "/menu",
                                search: "?id_user=" + data.id_user,
                                state: { fromDashboard: true }
                            }}>
                                <div className="storeName-map">
                                    <p>{key + 1}. </p>
                                    <p>{data.namestore}</p>
                                </div>
                            </Link>
                        )
                    }) : search.map((data, key) => {
                        return (
                            <Link to={{
                                pathname: "/menu",
                                search: "?id_user=" + data.id_user,
                                state: { fromDashboard: true }
                            }}>
                                <div className="storeName-map">
                                    <p>{key + 1}. </p>
                                    <p>{data.namestore}</p>
                                </div>
                            </Link>
                        )
                    })}
                </div>

            </div>
        </div>
    )
}

export default MenuStore
